
import React, { useState, useEffect, useRef } from 'react';
import { useCollection } from 'react-firebase-hooks/firestore';
import { collection, query, orderBy, where, Timestamp } from 'firebase/firestore';
import { db } from '../lib/firebase';
import { UserProfile } from '../types';
import { format } from 'date-fns';
import { Loader2, User } from 'lucide-react';

interface Message {
    id: string;
    userId: string;
    text: string;
    direction: 'in' | 'out';
    timestamp: number;
}

export const ChatView = () => {
    const [selectedUser, setSelectedUser] = useState<UserProfile | null>(null);

    // 1. Fetch Users
    const [usersSnapshot, usersLoading] = useCollection(collection(db, 'users'));
    const users = usersSnapshot?.docs.map(doc => ({ uid: doc.id, ...doc.data() } as UserProfile)) || [];

    // Filter out users without phone numbers (if any) or just show all
    const chatUsers = users.filter(u => u.phoneNumber);

    return (
        <div className="flex flex-col h-[calc(100vh-180px)] md:h-[600px] w-full bg-white rounded-xl shadow-sm overflow-hidden border border-slate-100">

            <div className="flex h-full">
                {/* Users List - Hidden on mobile if chat is open */}
                <div className={`${selectedUser ? 'hidden md:flex' : 'flex'} flex-col w-full md:w-1/3 border-r border-slate-100 bg-slate-50 h-full`}>
                    <div className="p-4 border-b border-slate-100 bg-white">
                        <h2 className="font-serif text-lg font-medium text-slate-800">Messages</h2>
                    </div>
                    <div className="flex-1 overflow-y-auto">
                        {usersLoading ? (
                            <div className="flex justify-center p-8"><Loader2 className="animate-spin text-slate-400" /></div>
                        ) : chatUsers.length === 0 ? (
                            <div className="p-4 text-center text-slate-500 text-sm">No contacts found.</div>
                        ) : (
                            chatUsers.map(user => (
                                <div
                                    key={user.uid || user.phoneNumber}
                                    onClick={() => setSelectedUser(user)}
                                    className={`p-4 border-b border-slate-100 cursor-pointer hover:bg-white transition-colors flex items-center gap-3 ${selectedUser?.phoneNumber === user.phoneNumber ? 'bg-white border-l-4 border-l-indigo-600' : ''}`}
                                >
                                    <div className="w-10 h-10 rounded-full bg-indigo-100 flex items-center justify-center text-indigo-600 flex-shrink-0">
                                        <User size={20} />
                                    </div>
                                    <div className="flex-1 min-w-0 text-left">
                                        <div className="font-medium text-slate-800 truncate">{user.displayName || user.phoneNumber}</div>
                                        <div className="text-xs text-slate-500 truncate">{user.phoneNumber}</div>
                                    </div>
                                </div>
                            ))
                        )}
                    </div>
                </div>

                {/* Chat Area */}
                <div className={`${!selectedUser ? 'hidden md:flex' : 'flex'} flex-col flex-1 bg-white h-full`}>
                    {selectedUser ? (
                        <ChatWindow user={selectedUser} onBack={() => setSelectedUser(null)} />
                    ) : (
                        <div className="hidden md:flex flex-col items-center justify-center h-full text-slate-400">
                            <div className="w-16 h-16 bg-slate-100 rounded-full flex items-center justify-center mb-4">
                                <User size={32} />
                            </div>
                            <p>Select a contact to view messages</p>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
};

const ChatWindow = ({ user, onBack }: { user: UserProfile, onBack: () => void }) => {
    // Fetch messages for this user
    const q = query(
        collection(db, 'messages'),
        where('userId', '==', user.phoneNumber),
        orderBy('timestamp', 'asc')
    );

    const [messagesSnapshot, loading] = useCollection(q);
    const messages = messagesSnapshot?.docs.map(d => ({ id: d.id, ...d.data() } as Message)) || [];

    const scrollRef = useRef<HTMLDivElement>(null);

    useEffect(() => {
        if (scrollRef.current) {
            scrollRef.current.scrollTop = scrollRef.current.scrollHeight;
        }
    }, [messages]);

    return (
        <div className="flex flex-col h-full w-full">
            {/* Header */}
            <div className="p-4 border-b border-slate-100 flex items-center gap-3 bg-white shadow-sm z-10 sticky top-0">
                <button onClick={onBack} className="md:hidden p-2 -ml-2 text-slate-500 hover:text-slate-800">
                    ←
                </button>
                <div className="w-8 h-8 rounded-full bg-indigo-100 flex items-center justify-center text-indigo-600 text-xs font-bold flex-shrink-0">
                    {user.displayName?.[0] || 'U'}
                </div>
                <div>
                    <div className="font-medium text-slate-800">{user.displayName || user.phoneNumber}</div>
                    <div className="text-xs text-slate-500">{user.phoneNumber}</div>
                </div>
            </div>

            {/* Messages */}
            <div className="flex-1 overflow-y-auto p-4 space-y-4 bg-slate-50 w-full" ref={scrollRef}>
                {loading ? (
                    <div className="flex justify-center p-8"><Loader2 className="animate-spin text-slate-400" /></div>
                ) : !messages || messages.length === 0 ? (
                    <div className="text-center text-slate-400 text-sm py-8">No message history</div>
                ) : (
                    messages.map((msg) => {
                        const isOut = msg.direction === 'out';
                        return (
                            <div key={msg.id} className={`flex w-full ${isOut ? 'justify-end' : 'justify-start'}`}>
                                <div className={`max-w-[85%] rounded-2xl px-4 py-3 text-sm shadow-sm ${isOut
                                        ? 'bg-indigo-600 text-white rounded-tr-none'
                                        : 'bg-white text-slate-800 border border-slate-100 rounded-tl-none'
                                    }`}>
                                    <div className="whitespace-pre-wrap break-words text-left">{msg.text}</div>
                                    <div className={`text-[10px] mt-1 text-right ${isOut ? 'text-indigo-200' : 'text-slate-400'}`}>
                                        {format(msg.timestamp, 'MMM d, h:mm a')}
                                    </div>
                                </div>
                            </div>
                        );
                    })
                )}
            </div>
        </div>
    );
}
